import request from '@/utils/request'
export function login (data) {
  return request({
    url: 'authorizations',
    method: 'post',
    data
  })
}
// 关注用户
export function followUser (data) {
  return request({
    url: '/user/followings',
    method: 'post',
    data: {
      target: data
    }
  })
}
// 取消关注用户
export function unfollowUser (target) {
  return request({
    url: `user/followings/${target}`,
    method: 'delete'
  })
}
// 获取用户自己的信息
export function getUserInfo () {
  return request({
    url: '/user'
  })
}
// 编辑用户照片资料
export function updateUserImage (data) {
  return request({
    url: '/user/photo',
    method: 'patch',
    data
  })
}
// 获取用户的基本信息
export function getUser () {
  return request({
    url: '/user/profile',
    method: 'get'
  })
}
// 编辑用户的基本信息
export function updateUser (data) {
  return request({
    url: '/user/profile',
    method: 'patch',
    data
  })
}
